import request from './javaRequest'

// 新增点单
export const addOrder = (data) => {
  return request({
    url: '/services/serveWork/add',
    data: data,
  })
}
// 获取风采列表
export const getStyleList = (data) => {
  return request({
    url: '/services/serveMien/page',
    data: data,
  })
}
// 获取我的风采列表
export const getMyStyleList = (data) => {
  return request({
    url: '/services/serveMien/selectMyServeMien',
    data: data,
  })
}
// 获取我的评价风采列表
export const getMyOderList = (data) => {
  return request({
    url: '/services/serveMien/selectOrderServeMien',
    data: data,
  })
}
// 发布新的服务风采
// /serveMien/add
export const addStyle = (data) => {
  return request({
    url: '/services/serveMien/add',
    data: data,
  })
}

// 发布服务评价
// /serveWork/appraise
export const addAppraise = (data) => {
  return request({
    url: '/services/serveWork/appraise',
    data: data,
    method: 'PUT',
  })
}

// 获取风采详情
// /serveMien/info/{serveMienId}
export const getStyleDetail = (serveMienId) => {
  return request({
    url: `/services/serveMien/info/${serveMienId}`,
    method: 'GET',
  })
}


// 风采点赞
// /serveWork/praise/{serveWorkId}
export const praise = (serveWorkId) => {
  return request({
    url: `/services/serveWork/praise/${serveWorkId}`,
    method: 'get',
  })
}

// 取消点赞
// /serveWork/cancelPraise/{serveWorkId}
export const cancelPraise = (serveWorkId) => {
  return request({
    url: `/services/serveWork/cancelPraise/${serveWorkId}`,
    method: 'get',
  })
}

// 获取详情评价
// /serveMien/selectAppraise/{serveMienId}
export const getAppraise = (serveMienId) => {
  return request({
    url: `/services/serveMien/selectAppraise/${serveMienId}`,
    method: 'get',
  })
}

// 获取详情评价
// /serveMien/selectAppraise/{serveMienId}
export const getMessAppraise = (serveMienId) => {
  return request({
    url: `/services/serveMienRecord/infoByServeWorkId/${serveMienId}`,
    method: 'get',
  })
}